export function renderResult(appEl, gameProgress) {
    var appHtml = "\n    <div class=\"wrap center\">\n    <div class=\"result\">\n        <div class=\"result__img\">\n        ".concat(gameProgress === 0 ? '<img src="static/img/выиграл.svg" alt="выиграл" />' : '<img src="static/img/проиграл.svg" alt="проиграл" />', "\n        </div>\n        <h2 class=\"result__title\">").concat(gameProgress === 0 ? 'Вы выиграли!' : 'Вы проиграли', "</h2>\n            <p class=\"result__timer-text\">\u0417\u0430\u0442\u0440\u0430\u0447\u0435\u043D\u043D\u043E\u0435 \u0432\u0440\u0435\u043C\u044F</p>\n            <div class=\"result__timer-time\">01.20</div>\n        <button class=\"button result__button\" type=\"submit\">\n            \u0418\u0433\u0440\u0430\u0442\u044C \u0441\u043D\u043E\u0432\u0430\n        </button>\n    </div>\n</div>\n");
    appEl.innerHTML = appHtml;
}
